header {
    background-color: rgba(65, 90, 119);
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    z-index: 2;
}

.links {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
}

p {
    position: relative;
}

.active {
    display: flex;
}

.activeLink::after {
    content: '';
    position: absolute;
    height: 1px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: linen;
    animation: underline 0.2s;
}

.buttonContainer {
    display: flex;
    flex-direction: row;
    gap: 20px
}

.expand {
    position: relative;
    scale: 40;
}

.btn {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: #415A77;
    text-align: center;
    line-height: 5rem;
    color: #E0E1DD;
    transition: scale 1s;
}

.btn:first-child {
    background-color: #2D3B51
}

.btn:nth-child(2) {
    background-color: #404F67;
}

.btn:nth-child(3) {
    background-color: #52647D;
}
.btn:last-child {
    background-color: #657893;
}

@keyframes underline {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }

}

