* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Rubik", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
}

section {
  height: 100vh;
  background-color: #E0E1DD;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: flex;
  visibility: hidden;
  opacity: 0;
  position: fixed;
  width: 100%;
  transition: opacity 0.3s;
}

.show {
  visibility: visible;
}

.fade-in {
  opacity: 1;
}