#projects {
    justify-content: start;
    padding: 1rem;
    gap: 4vmin;
}

.back {
    color: black;
    background: none;
    border: none;
    font-size: 30px;
    position: fixed;
    top: 40px;
    left: 40px;
}

.card-container {
    display: flex;
    flex-direction: row;
    gap: 4vmin;
    transform: translate(0, 0);
}