.intro {
    margin: 0;
    font-family: "Annapurna SIL", serif;
    font-weight: 400;
    font-style: normal;
    font-size: var(--fs-xl);
    line-height: var(--fs-xl);
}

h2 {
    margin: 0;
    font-size: 1rem;
}

.subheader {
    display: flex;
    gap: 20px;
}

.subheader h2:not(:first-child):before {
    content: "";
    border: 1px solid black;
    align-self: stretch;
    margin-right: 20px;
}

#about {
    display: flex;
    flex-direction: row;
    padding: 1rem;
    align-items: center;
    visibility: visible;
    opacity: 1;
}

.aboutInfo {
    display:flex;
    flex-direction: row;
    flex: 0 0 auto;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.image_container {
    height: 33vw;
    width: 33vw;
    border-radius: 50%;
    overflow: hidden;
}

.text-container {
    flex-grow: 1;
    width: 100%;
}

.profile-img {
    height: calc(100% + 5px);
    width: calc(100% + 5px);;
    object-fit: contain;
    border-radius: 50%;
}

@media only screen and (max-width : 1024px) {
    #about {
        align-items: flex-start;
    }

    .aboutInfo {
        flex-direction: column;
        align-items: center;
    }

    .image_container {
        width: 45vw;
        height: 45vw;
    }
}

* {
    color: #0D1B2A;
}