.card {
    height: 62vmin;
    aspect-ratio: 3/4;
    perspective: 1000px;
    background-color: transparent;
    filter: drop-shadow(3px 3px 10px #555555);
    user-select: none;
}

.card-front:hover .card-img {
    filter: brightness(50%);
    transform: scale(1.02);
}

.card-front:hover > .card-title {
    transition: opacity 0.3s ease-in 0.2s;
    opacity: 1;
}


.flipped {
    transform: rotateY(180deg);
}

.card-content {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}


.card-back {
    background-color: #2D3B51;
    transform: rotateY(180deg);
}

.card-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease, filter 0.3s ease;
}

.card-title {
    position: absolute;
    width: 100%;
    font-size: 3rem;
    color: #E0E1DD;
    display: block;
    opacity: 0;
    z-index: 1;
    margin: 1rem 0;
    font-family: "Annapurna SIL", serif;
    transition: opacity 0.3s ease-out 0s;
}

.flipped {
    transform: rotateY(180deg);
}